<template>
    <div>
        <h2>订单审核页面</h2>
        <div id="content-list">
            <!--  -->
            <div>
                <template>
                    <el-table
                        border
                        ref="multipleTable"
                        :data="tableData"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="handleSelectionChange">
                        <el-table-column
                        fixed=""
                        type="selection"
                        width="55">
                        </el-table-column>
                        <el-table-column
                        prop="date"
                        sortable
                        :filter-method="filterHandler"
                        label="日期"
                        width="120">
                        <!-- <template slot-scope="scope">{{ scope.row.date }}</template> -->
                        </el-table-column>
                        <el-table-column
                        prop="clientID"
                        label="账户编号"
                        width="120">
                        </el-table-column>
                        <el-table-column
                        prop="accountID"
                        label="策略编号"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="afterAction"
                        label="结束后是否继续执行"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="clOrdID"
                        label="委托编号"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="effectiveTime"
                        label="订单生效时间"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="expireTime"
                        label="订单终止时间"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="limitAction"
                        label="涨停跌停"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="symbol"
                        label="证券代码"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="side"
                        label="买/卖"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="securityExchange"
                        label="交易所类型"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="ordType"
                        label="委托类型"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="securityType"
                        label="证券类型"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="orderQty"
                        label="委托股数"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="price"
                        label="委托价格"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="transactTime"
                        label="委托时间"
                        show-overflow-tooltip>
                        </el-table-column>
                    </el-table>
                    <div style="margin-top: 20px">
                        <el-button type="primary" @click="censorClick">确认审核</el-button>
                        <el-button type="info" @click="toggleSelection()">取消选择</el-button>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import common from "@/utils/common"
export default {
    // 注入reload方法刷新页面
    inject:['reload'],
    name:'orderQuantity',
    data(){
        return {
            userID:'',
            // 
            tableData:[],
            censorDate:[],//选择要审核的date
            dateID:'',
        }
    },
    methods:{
        // 时间戳转化为时间
        timestampToTimes(timestamps) {
            let date = new Date(timestamps);//时间戳为10位需*1000，时间戳为13位的话不需乘1000
            let Y = date.getFullYear() + '-';
            let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
            let D = date.getDate() + ' ';
            let h = date.getHours() + ':';
            let m = date.getMinutes() + ':';
            let s = date.getSeconds();
            return Y+M+D+h+m+s;
        },
        // 
        toggleSelection(rows) {
            console.log(rows)
            if (rows) {
            rows.forEach(row => {
                this.$refs.multipleTable.toggleRowSelection(row);
            });
            } else {
            this.$refs.multipleTable.clearSelection();
            }
        },
        // 获取要选择审核行的信息
        handleSelectionChange(val) {
            console.log(val)
            this.censorDate = val;
            console.log(this.censorDate)
        },
        // 排序
        filterHandler(value, row, column) {
            const property = column['property'];
            return row[property] === value;
        },
        // 获取要审核的订单
        // timestampToTime(1403058804);
        // console.log(timestampToTime(1403058804));//2014-06-18 10:33:24
        getData:function(){
            this.userID = common.decipher('wsUserID')
            console.log('11111111111111111111',domain.ordQueryOrder+'/'+this.userID)
            axios
            .get(domain.ordQueryOrder+'/'+this.userID)
            .then((response) => {
                console.log(response,response.data)
                if (response.data == '400') {
                    common.altPrompt('获取数据失败，请重试');
                    return false;
                } else {
                    for (let i = 0; i < response.data.length; i++) {
                        response.data[i].effectiveTime = this.timestampToTimes(response.data[i].effectiveTime)
                        response.data[i].expireTime = this.timestampToTimes(response.data[i].expireTime)
                        response.data[i].transactTime = this.timestampToTimes(response.data[i].transactTime)
                        this.tableData.push(response.data[i])
                    }
                }
            })
            .catch((err) => {
                console.log(err)
            })
            console.log(this.tableData)
        },
        // 点击确认审核
        censorClick(){
            console.log(this.censorDate,this.censorDate,domain.ordSendOrder,)
            for (let i = 0; i < this.censorDate.length; i++) {
                this.dateID = this.censorDate[i].id;
                console.log(this.dateID)
                axios
                .post(domain.ordSendOrder,
                {
                    id:this.dateID
                })
                .then((response) => {
                    // 400审核失败，200审核成功
                    console.log(response)
                    if (response.data == '200') {
                        common.altPrompt('审核成功')
                        this.reload();//调用刷新方法
                        return false;
                    } else if(response.data == '400'){
                        common.altPrompt('审核失败')
                        return false;
                    }
                })
                .catch((error) => {
                    console.log(error)
                })
            }
        },
    },
    mounted: function() {
        console.log("el被新创建的$el替换，并挂载到实例上去之后调用本钩子");
        this.getData();
    },
}
</script>

<style>

</style>



