<template>
    <div>
        <h2>普通单审核</h2>
        <div id="comRevo">
            <!--  -->
            <div>
                <template>
                    <el-table
                        border
                        ref="multipleTable"
                        :data="comRevoData"
                        tooltip-effect="dark"
                        style="width: 100%"
                        @selection-change="handleSelChangecomRevoData">
                        <el-table-column
                        fixed=""
                        type="selection"
                        width="55">
                        </el-table-column>
                        <el-table-column
                        prop="date"
                        sortable
                        :filter-method="filterHandlercomRevoData"
                        label="测试数据、日期"
                        width="120">
                        </el-table-column>
                        <el-table-column
                        prop="clientID"
                        label="账户编号"
                        width="120">
                        </el-table-column>
                        <el-table-column
                        prop="accountID"
                        label="策略编号"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="clOrdID"
                        label="委托编号"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="effectiveTime"
                        label="订单生效时间"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="expireTime"
                        label="订单终止时间"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="symbol"
                        label="证券代码"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="side"
                        label="买/卖"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="securityExchange"
                        label="交易所类型"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="ordType"
                        label="委托类型"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="securityType"
                        label="证券类型"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="orderQty"
                        label="委托股数"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="price"
                        label="委托价格"
                        show-overflow-tooltip>
                        </el-table-column>
                        <el-table-column
                        prop="transactTime"
                        label="委托时间"
                        show-overflow-tooltip>
                        </el-table-column>
                    </el-table>
                    <div style="margin-top: 20px">
                        <el-button type="primary" @click="surecomRevoClick">确认审核</el-button>
                        <el-button type="info" @click="toggleSeleccomRevo()">取消选择</el-button>
                    </div>
                </template>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    // 注入reload方法刷新页面
    inject:['reload'],
    name:'commonRevocation',
    data(){
        return{
            comRevoData:[],//所有审核数据
            choicecomRevoData:[],//选择的审核数据
            comRevoClientID:'',//选择审核的数据里面的clientID
        }
    },
    methods:{
        // 获取普通单要审核的数据
        getcomRevoDatas(){
            console.log('获取普通单审核数据')
            this.comRevoData=[
                {
                    date:'2018-09-11',
                    clientID:'001',
                    accountID:'001',
                    clOrdID:'001',
                    effectiveTime:'2011-11-11',
                    expireTime:'2011-11-11',
                    symbol:'001',
                    side:'买',
                    securityExchange:'001',
                    ordType:'001',
                    securityType:'001',
                    orderQty:'001',
                    price:'001',
                    transactTime:'2011-11-11',
                },{
                    date:'2018-08-15',
                    clientID:'002',
                    accountID:'001',
                    clOrdID:'001',
                    effectiveTime:'2011-11-11',
                    expireTime:'2011-11-11',
                    symbol:'001',
                    side:'买',
                    securityExchange:'001',
                    ordType:'001',
                    securityType:'001',
                    orderQty:'001',
                    price:'001',
                    transactTime:'2011-11-11',
                },{
                    date:'2018-10-10',
                    clientID:'003',
                    accountID:'001',
                    clOrdID:'001',
                    effectiveTime:'2011-11-11',
                    expireTime:'2011-11-11',
                    symbol:'001',
                    side:'买',
                    securityExchange:'001',
                    ordType:'001',
                    securityType:'001',
                    orderQty:'001',
                    price:'001',
                    transactTime:'2011-11-11',
                },{
                    date:'2018-07-07',
                    clientID:'004',
                    accountID:'001',
                    clOrdID:'001',
                    effectiveTime:'2011-11-11',
                    expireTime:'2011-11-11',
                    symbol:'001',
                    side:'买',
                    securityExchange:'001',
                    ordType:'001',
                    securityType:'001',
                    orderQty:'001',
                    price:'001',
                    transactTime:'2011-11-11',
                },{
                    date:'2018-12-12',
                    clientID:'005',
                    accountID:'001',
                    clOrdID:'001',
                    effectiveTime:'2011-11-11',
                    expireTime:'2011-11-11',
                    symbol:'001',
                    side:'买',
                    securityExchange:'001',
                    ordType:'001',
                    securityType:'001',
                    orderQty:'001',
                    price:'001',
                    transactTime:'2011-11-11',
                }
            ]
        },
        // 获取要选择审核行的信息
        handleSelChangecomRevoData(val) {
            console.log(val)
            this.choicecomRevoData = val;
            console.log(this.choicecomRevoData)
        },
        // 点击确认审核
        surecomRevoClick(){
            console.log('确认审核',this.choicecomRevoData)
            for (let i = 0; i < this.choicecomRevoData.length; i++) {
                this.comRevoClientID = this.choicecomRevoData[i].clientID;
                console.log(this.comRevoClientID)
                // 发送请求,审核成功后=>this.reload();//调用刷新方法
                this.reload();
            }
        },
        // 取消选择
        toggleSeleccomRevo(rows) {
            console.log(rows)
            if (rows) {
            rows.forEach(row => {
                this.$refs.multipleTable.toggleRowSelection(row);
            });
            } else {
            this.$refs.multipleTable.clearSelection();
            }
        },
        // 排序
        filterHandlercomRevoData(value, row, column) {
            const property = column['property'];
            return row[property] === value;
        },
    },
    mounted:function(){
        // 获取普通单要审核的数据
        this.getcomRevoDatas();
    }
}
</script>

<style>

</style>



