<template>
    <div id="revoChoice">
        <ul class="tabs">
            <li class="li-tab" v-for="(item,index) in tabsParam" 
            @click="toggleTabs(index)"
            :class="{active:index!=nowIndex}" :key="item">{{item}}</li>
        </ul>
        <span class="revoChoice" @click="loginAginRev">退出系统</span>
        <!-- 算法单撤销 -->
        <div class="divTab" v-show="nowIndex===0">
            <orderQuantity></orderQuantity>
        </div>
        <!-- 普通单撤销 -->
        <div class="divTab" v-show="nowIndex===1">
            <commonRevocation></commonRevocation>
        </div>
    </div>
</template>

<script>
import orderQuantity from "@/page/orderQuantity/orderQuantity";
import commonRevocation from "@/page/commonRevocation/commonRevocation";
export default {
    name:'tabsRevocation',
    data() {
        return {
            tabsParam: ["算法单审核", "普通单"], //（这个也可以用对象key，value来实现）
            nowIndex: 1 //默认第一个tab为激活状态
        };
    },
    components: {
        orderQuantity,
        commonRevocation
    },
    methods: {
        toggleTabs: function(index) {
        this.nowIndex = index;
        },
        // 跳转首页
        loginAginRev(){
        this.$router.push({
            name:'login',
            param:''
        })
        },
    }
}
</script>

<style>
.active {
  background: #eee;
}
.tabs {
  /* width: 200px;
        height: 20px; */
  font: 0;
  padding: 0;
}
.li-tab {
  width: 50%;
  height: 100%;
  display: inline-block;
  text-align: center;
}
</style>



